import {
  AfterContentInit,
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  Renderer2,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewChildren
} from '@angular/core';
import {transition, trigger, useAnimation} from '@angular/animations';
import {asyncScheduler, noop, ReplaySubject, Subject} from 'rxjs';
import {takeUntil, throttleTime} from 'rxjs/operators';
import {cloneDeep, isArray} from 'lodash-es';

import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularCustomEventType,
  AdaptRadarAngularService,
  AdaptRadarAngularGenericDirective
} from '../common/radar/adapt-radar';

import {AdaptDockedPanelService, DockedPanel} from '../docked-panel/index';
import {AdaptDropdownDirective} from '../dropdown/index';
import {AdaptProfileComponent} from '../profile/profile.component';
import {AdaptTranslateService} from '../common/i18n/index';
import {AlertAnimatedConfig, AlertCloseEvent} from '../alert-animated/index';
import {MenuGlobalService} from '../menu/menu-global.service';
import {MenuRadarMetadata, MenuItem} from '../menu/index';
import {ProfileModel} from '../profile/index';

import {ANIMATION_NAME} from '../common/animation';
import {AdaptDeviceDetectionService} from '../common/device-detection.service';
import {KeyValueObject} from '../common/common.models';
import {ResizeService} from '../common/resize/resize.service';
import {SafeCdr} from '../common/safe-cdr';
import {checkURL} from '../common/check-url';
import {isDefined, maxDepth} from '../common/utilities';

import {NavigationAction, NavigationMetadataTextsModel} from './navigation.config';
import {AdaptNavActionsComponent} from './nav.actions.component';
import {AdaptNavigationService, LoaderState} from './navigation.service';

@Component({
  selector: 'adapt-navigation',
  templateUrl: './navigation.component.html',
  styles: [`
    .adapt-profile {
      display: inline-block;
      margin-right: -10px;
      margin-left: auto;
      -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
      flex: 0 0 auto;
    }

    .a-actions + .adapt-profile {
      margin-left: 0;
    }

    .navigation-wrapper {
        width: 100%;
        display: flex;
    }
    `
  ],
  animations: [
    trigger('loaderAnimation', [
      transition('void => *', [
        useAnimation(ANIMATION_NAME.height)
      ]),
      transition('* => void', [
        useAnimation(ANIMATION_NAME.opacity, {
          params: {
            from: '*',
            to: '0'
          }
        })
      ])
    ]),
    trigger('loaderMessageAnimation', [
      transition('void => *', [
        useAnimation(ANIMATION_NAME.moveIn, {params: {translateY: -50}})
      ]),
      transition('* => void', [
        useAnimation(ANIMATION_NAME.moveOut, {params: {translateY: -50}})
      ])
    ])
  ]
})
export class AdaptNavigationComponent extends AdaptRadarAngularGenericDirective implements OnInit, AfterContentInit, AfterViewInit, OnChanges, OnDestroy {
  alerts: AlertAnimatedConfig[] = [];
  loadingRun: boolean = false;
  loadingMessage: string = '';
  floating: boolean = false;
  bizarreZone: boolean = false;
  customProfilePresent: boolean = false;
  iphoneCSSclass: string = '';
  textColor: string;

  margin: string = '';

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  filtering: any = {
    name: ''
  };

  /**
   * DockedPanel service
   */
  hamburgerService: AdaptDockedPanelService;

  /**
   * DockedPanel config
   */
  hamburgerMenuConfig: DockedPanel;

  /**
   * DockedPanel open indicator
   */
  hamburgerMenuOpen: boolean = false;

  // TODO: Refactor this if possible
  /* eslint-disable @typescript-eslint/no-explicit-any */
  /**
   * copy of menu model, to convert into the tree
   */
  menuModel: any;
  /* eslint-enable @typescript-eslint/no-explicit-any */

  /**
   * current active id of the Menu
   */
  currentActiveId: string = null;

  /**
   * hamburger mode - is collapsed mode
   */
  hamburgerMode: boolean = false;
  kebabMode: boolean = false;

  defaultTexts: NavigationMetadataTextsModel = {};

  /**
   * references to ngDropdownMenu
   */
  @ViewChildren(AdaptDropdownDirective) allDropdownsDirectives: QueryList<AdaptDropdownDirective>;

  /**
   * reference to menu element
   */
  @ViewChild('menuEl', {static: false}) menuEl: ElementRef;

  /**
   * reference to logo container element
   */
  @ViewChild('logoEl', {static: false}) logoEl: ElementRef;

  @ViewChild('hamburgerBtn', {static: false}) hamburgerBtn: ElementRef;
  @ViewChild('hamburgerLogo', {static: false}) hamburgerLogo: ElementRef;

  @ViewChild('alertsSection', {static: true}) alertsSection: ElementRef;

  /**
   * reference to actions element
   */
  @ViewChild('actionsEl', {static: true}) actionsEl: AdaptNavActionsComponent;

  /**
   * reference to profile element
   */
  @ViewChild('profileEl', {static: false}) profileEl: AdaptProfileComponent;

  /**
   * reference to hamburgerContent element
   */
  @ViewChild('hamburgerContent', {static: false}) hamburgerContent: ElementRef;

  /**
   * reference to hamburger menu template
   */
  @ViewChild('hamburgerMenuTemplate', {static: true}) hamburgerMenuTemplate: TemplateRef<null>;

  /**
   * reference to loader
   */
  @ViewChild('loaderSection', {static: true}) loaderSection: ElementRef<HTMLDivElement>;

  /**
   * reference to alerts
   */
  @ViewChild('floatSection', {static: true}) floatSection: ElementRef<HTMLDivElement>;

  /**
   * reference to floatWrapper
   */
  @ViewChild('floatWrapper', {static: true}) floatWrapper: ElementRef<HTMLDivElement>;

  // Shadow nav elements
  @ViewChild('navElShadow', {static: true}) navElShadow: ElementRef<HTMLElement>;
  @ViewChild('navElContentShadow', {static: true}) navElContentShadow: ElementRef<HTMLElement>;
  @ViewChild('profileElShadow', {static: false}) profileElShadow: ElementRef<HTMLElement>;
  @ViewChild('actionsElShadow', {static: true}) actionsElShadow: ElementRef<HTMLElement>;
  @ViewChild('menuElShadow', {static: true}) menuElShadow: ElementRef<HTMLElement>;
  @ViewChild('hamburgerBtnElShadow', {static: true}) hamburgerBtnElShadow: ElementRef<HTMLElement>;
  @ViewChild('logoElShadow', {static: true}) logoElShadow: ElementRef<HTMLElement>;

  /**
   * Input which defines menu items
   */
  @Input()
  set menu(value: MenuItem[]) {
    if (isArray(value)) {
      this._menu = cloneDeep(value);
      this._menu.forEach(menuItem => this._extendProps(menuItem));
    }
  }

  get menu(): MenuItem[] {
    return this._menu;
  }

  /**
   * Input which defines metadata
   */
  @Input() metadata: NavigationMetadataTextsModel;

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() config: any;

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() kebabView: any;

  /**
   * minimal gap between menu element and actions/profile
   */
  @Input() gapWidth: number;
  @Input() container: boolean;
  @Input() actions: NavigationAction[];
  @Input() profile: ProfileModel;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() customProfile: TemplateRef<any>;

  @Input() appSwitcherEnabled: boolean;

  @Input() appSwitcherMetaData: NavigationAction;

  @Input() set theme(v: string) {
    this._theme = v;
    this.themeClass = v;
  }

  get theme(): string {
    return this._theme;
  }

  set themeClass(v: string) {
    this.textColor = getComputedStyle(document.documentElement).getPropertyValue('--nav-links-color');

    switch (v.toLowerCase()) {
      case 'grey':
        this._themeClass = 'a-nav__grey';
        break;
      case 'white':
        this._themeClass = 'a-nav__white';
        break;
      case '':
        this._themeClass = '';
        break;
      case 'dark':
        this._themeClass = '';
        break;
      default:
        this._themeClass = 'a-nav__custom-color';
    }
  }

  get themeClass(): string {
    return this._themeClass;
  }

  @Output() readonly alertClosed: EventEmitter<AlertCloseEvent> = new EventEmitter<AlertCloseEvent>();

  private _menu: MenuItem[];
  private _themeClass: string = '';
  private _theme: string = '';
  private _destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);
  private _removeDocumentScrollListener: () => void;
  private _menuTestIdIndex: number = 1;


  constructor(dps: AdaptDockedPanelService,
              private el: ElementRef,
              private _resizeService: ResizeService,
              private deviceDetection: AdaptDeviceDetectionService,
              private _navService: AdaptNavigationService,
              private renderer: Renderer2,
              private zone: NgZone,
              private _menuGlobalService: MenuGlobalService,
              private _changeDetectorRef: ChangeDetectorRef,
              protected _adaptRadarService: AdaptRadarAngularService,
              ts: AdaptTranslateService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptNavigation);

    this.hamburgerMode = deviceDetection.isMobile();
    this.hamburgerService = dps;
    this.container = false;
    this.gapWidth = 20;
    this.setDefaultTexts(ts.getCurrentLanguage());
  }

  ngOnInit(): void {
    if (this.config) {
      this.container = this.config.container || false;
      this.gapWidth = this.config.gapWidth || 20;
    }

    this.iphoneCSSclass = this.deviceDetection.deviceIphone() ? 'h-100' : '';

    this.customProfilePresent = !!this.customProfile;

    // create copy of menu and convert it to Tree
    this.menuModel = {
      subMenu: this.menu
    };

    this.addParent(this.menu, this.menuModel);

    this._navService.loaderState
      .pipe(takeUntil(this._destroyed$))
      .subscribe(this._loaderSubscriptionHandler.bind(this));

    this._navService.alertsState
      .pipe(takeUntil(this._destroyed$))
      .subscribe(this._alertSubscriptionHandler.bind(this));

    this.zone.runOutsideAngular(() => {
      this._removeDocumentScrollListener = this.renderer.listen('document', 'scroll', this._checkFloating.bind(this));
    });
    this.metadata = {...this.defaultTexts, ...this.metadata};

    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptNavigation, {
      EventType: AdaptRadarAngularCustomEventType.Init,
      NavigationColor: this.theme,
      Logo: this.metadata.logoClass,
      MenuCount: this.menu?.length,
      ActionCount: this.actions?.length,
      HamburgerMode: this.hamburgerMode,
      Title: this.metadata.productName,
      ActionsLoaded: this.actions?.map(action => action.name || action.title)
    });
  }

  setDefaultTexts(translation: KeyValueObject): void {
    this.defaultTexts.skipText = translation['adapt.navigation.skip'];
    this.defaultTexts.menuText = translation['adapt.navigation.menu'];
    this.defaultTexts.backBtn = translation['adapt.common.back'];
    this.defaultTexts.switcher = {
      notFoundText: translation['adapt.navigation.notFound'],
      filter: {
        placeholder: translation['adapt.common.search'],
        btnClearText: translation['adapt.common.clear'],
        btnSearchText: translation['adapt.common.search'],
        labelText: ''
      }
    };
  }

  alertsHeightChanged(height: number): void {

    this.margin = height + 'px';

    if (this.bizarreZone) {
      this.floatSection.nativeElement.classList.add('a-margin-transition');
    }

    this.setMargin();
  }

  setMargin(): void {
    if (!this.floating) {
      this.renderer.setStyle(this.floatSection.nativeElement, 'margin-bottom', '-2px');
    } else {
      this.renderer.setStyle(this.floatSection.nativeElement, 'margin-bottom', this.margin);
    }
  }

  /**
   * Recursive. Makes a Tree from the Menu object
   * parent - parent Node
   */
  addParent(subArray: MenuItem[], parent: MenuItem): void {
    for (const subArr of subArray) {

      subArr.parent = parent;

      if (subArr.items) {
        this.addParent(subArr.items, subArr);
      }
    }
  }

  // TODO: Refactor this if possible
  /* eslint-disable @typescript-eslint/no-explicit-any */
  /**
   * Recursive. Search Menu tree for the Node with given Id
   */
  searchTree(element: any, matchingId: any): null | any {
    /* eslint-enable @typescript-eslint/no-explicit-any */
    if (parseInt(element.id, 10) === matchingId) {
      return element;
    } else if (element.subMenu !== null) {

      let i;
      let result = null;

      if (element.subMenu && element.subMenu.length) {
        for (i = 0; result === null && i < element.subMenu.length; i++) {
          result = this.searchTree(element.subMenu[i], matchingId);
        }
      }

      return result;
    }

    return null;
  }

  /**
   * add resize listener to global nav
   */
  ngAfterContentInit(): void {
    const navResize$ = new Subject();

    navResize$
      .pipe(
        takeUntil(this._destroyed$),
        throttleTime(100, asyncScheduler, {trailing: true})
      )
      .subscribe(() => {
        this.zone.run(() => {
          this.checkForHamburger();
          SafeCdr.detectChanges(this._changeDetectorRef);
        });
      });

    this._resizeService.addResizeEventListener(this.el.nativeElement, navResize$.next.bind(navResize$));
    this._resizeService.addResizeEventListener(this.navElContentShadow.nativeElement, navResize$.next.bind(navResize$));
  }

  ngOnChanges(changes: SimpleChanges): void {

    if (changes.metadata) {
      this.metadata = {...this.defaultTexts, ...this.metadata};
    }

    if (changes.menu) {
      // need to check of menu is loaded afterwards and need to rebuild the tree
      this.menuModel = {
        subMenu: this.menu
      };
    }
  }

  /**
   * in this hook we are saving width of menu element to determine later if it fits in header or not
   */
  ngAfterViewInit(): void {
    super.ngAfterViewInit();

    this.hamburgerMenuConfig = {
      content: this.hamburgerMenuTemplate,
      direction: 'left',
      blockDismiss: false
    };

    // Throw 'inverted' prop to the inner profile component
    if (this.profileEl) {
      this.profileEl.profile.inverted = !!this._themeClass;
    }

    this._verifyLogo();
  }

  /**
   * remove resize listener
   */
  ngOnDestroy(): void {
    this._resizeService.removeResizeEventListener(this.el.nativeElement);
    this._resizeService.removeResizeEventListener(this.navElContentShadow.nativeElement);

    if (this._removeDocumentScrollListener) {
      this._removeDocumentScrollListener();
    }

    this._destroyed$.next(true);
    this._destroyed$.complete();
  }

  /**
   * check hamburger mode
   */
  @Input() checkForHamburger(): void {
    const isHamburgerMode = this.navElContentShadow.nativeElement.offsetWidth + this.gapWidth > this.navElShadow.nativeElement.offsetWidth;
    const navContainerWidth = this.navElShadow.nativeElement.offsetWidth;

    // If hamburger mode changed send event to analytics system
    if (isHamburgerMode !== this.hamburgerMode) {
      this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptNavigationHamburger, {
        EventType: AdaptRadarAngularCustomEventType.ValueChanged,
        HamburgerMode: this.hamburgerMode,
        ContainerSize: navContainerWidth,
        MenuCount: this.menu?.length,
        ActionCount: this.actions?.length,
        ProfilePresent: !!this.profile
      });
    }

    this.hamburgerMode = isHamburgerMode;

    this.kebabMode = this.hamburgerMode ?
      this.navElContentShadow.nativeElement.offsetWidth -
      this.menuElShadow.nativeElement.offsetWidth -
      (this.profileElShadow?.nativeElement.offsetWidth || 0) -
      this.logoElShadow.nativeElement.offsetWidth +
      this.logoEl.nativeElement.offsetWidth +
      this.gapWidth > navContainerWidth : false;
  }

  showSubmenu(id: string): void {
    this.hamburgerContent.nativeElement.querySelector('#' + id).classList.toggle('show');
    this.hamburgerContent.nativeElement.classList.toggle('submenu');
    this.filtering.name = '';
  }

  setFocusToAnchor(el: HTMLDivElement): void {
    el.focus();
  }

  /**
   * Close all submenus and opens current
   */
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  toggleSubmenu(event: any): void {
    this.closeAllSubmenu();
    event.target.classList.toggle('submenuOpen');
  }

  /**
   * Traverses all opened submenus and closes them
   */
  closeAllSubmenu(): void {
    const elements = this.menuEl.nativeElement.querySelectorAll('.submenuOpen');

    // TODO: Refactor this if possible
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    Array.prototype.map.call(elements, function (item: any): any {
      item.classList.remove('submenuOpen');
    });
    this.filtering.name = '';
  }

  /**
   * clears the filter
   */
  clearFilter(searchInputElement: HTMLElement): void {
    this.filtering.name = '';
    searchInputElement.focus();
  }

  handleMenuLinkSelection(item: MenuItem): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptNavigationMenu, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      Name: item.label,
      Disabled: item.disabled,
      Type: 'link',
      MaxDepth: maxDepth(this.menu, 'subMenu'),
      SiblingsOptionsCount: this._getSiblingsCount(),
      HiddenSiblingsOptionsCount: this._getHiddenSiblingsCount()
    });
  }

  handleMenuRouteSelection(item: MenuItem, isAction: boolean = false, closeHamburger: boolean = true): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptNavigationMenu, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      Name: item.label,
      Disabled: item.disabled,
      Type: 'route',
      MaxDepth: maxDepth(this.menu, 'subMenu'),
      SiblingsOptionsCount: this._getSiblingsCount(),
      HiddenSiblingsOptionsCount: this._getHiddenSiblingsCount()
    });

    this.selectMenuItem(item.id, isAction);

    if (closeHamburger) {
      this.closeHamburger();
    }
  }

  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  handleMenuActionSelection(event: any, item: MenuItem, invokeSelectMenuItem: boolean = false, isAction: boolean = false, closeHamburger: boolean = true): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptNavigationMenu, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      Name: item.label,
      Disabled: item.disabled,
      Type: 'action',
      MaxDepth: maxDepth(this.menu, 'subMenu'),
      SiblingsOptionsCount: this._getSiblingsCount(),
      HiddenSiblingsOptionsCount: this._getHiddenSiblingsCount()
    });

    if (invokeSelectMenuItem) {
      this.selectMenuItem(item.id, isAction);
    }

    item.action(event, item.id);

    if (closeHamburger) {
      this.closeHamburger();
    }
  }

  // TODO: Refactor this if possible
  /* eslint-disable @typescript-eslint/no-explicit-any */
  /**
   * Set active state to the Menu item and all parent Node in the Tree
   */
  @Input() selectMenuItem(id: any, isAction: boolean = false): void {
    /* eslint-enable @typescript-eslint/no-explicit-any */

    if (!isAction) {
      this._menuGlobalService.removeActiveState();
    }

    if (isDefined(id)) {
      // remove all previous active
      let oldNode = this.searchTree(this.menuModel, this.currentActiveId);

      while (oldNode) {
        oldNode.active = false;
        oldNode = oldNode.parent;
      }
      this.currentActiveId = id;

      let node = this.searchTree(this.menuModel, id);

      while (node) {
        node.active = true;
        node = node.parent;
      }
    }

    // close all open dropdowns
    if (this.allDropdownsDirectives) {
      this.allDropdownsDirectives.forEach((item) => {
        item.close();
      });
    }
  }

  /**
   * open menu
   */
  openHamburger(): void {
    this.hamburgerService.open(this.hamburgerMenuConfig)
      .then(() => {
        this.hamburgerMenuOpen = false;
      }, () => {
        this.hamburgerMenuOpen = false;
      });

    this.hamburgerMenuOpen = true;
  }

  /**
   * closes menu
   */
  @Input() closeHamburger(): void {
    this.hamburgerService.closeAllDockedPanels();
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  getTarget(item: any): string {
    return item.target ? item.target : '_self';
  }

  getAnalyticsMetadata(): MenuRadarMetadata {
    return {
      parentComponentName: AdaptRadarAngularCustomEventName.AdaptNavigation,
      siblingsCount: this._getSiblingsCount(),
      hiddenSiblingsCount: this._getHiddenSiblingsCount()
    };
  }

  trackByFnMenu(index: number, item: MenuItem): string {
    return item.id;
  }

  trackByFnActions(index: number, item: NavigationAction): NavigationAction {
    return item;
  }

  private _extendProps(item: MenuItem): void {
    item.label = item.label || item.name;
    item.routerLink = item.routerLink || item.route;
    item.items = item.items || item.subMenu;

    if (item.items) {
      item.items.forEach(i => this._extendProps(i));
    }
  }

  /**
   * Checks whether provided url() data type is valid and image can be loaded.
   * Otherwise, replace with the default bmc logo
   */
  private _verifyLogo(): void {
    if (this.logoEl) {
      const logoElem = this.logoEl.nativeElement.querySelector('.a-product__logo');
      const logoBgImage = getComputedStyle(logoElem).backgroundImage;

      if (!logoBgImage.includes('url')) {
        return;
      }

      // retrieve a string between parentheses
      const parenthesesRegExp: RegExp = /(?:\(['"]?)(.*?)(?:['"]?\))/;
      const url = parenthesesRegExp.exec(logoBgImage)[1];

      checkURL(url)
        .pipe(takeUntil(this._destroyed$))
        .subscribe(
          noop,
          () => {
            this.logoEl.nativeElement.querySelector('.a-product__logo').classList.add('logo-bmc');
            this.logoElShadow.nativeElement.querySelector('.a-product__logo').classList.add('logo-bmc');
          }
        );
    }
  }

  /**
   * Checks alerts position according to the content scroll
   */
  private _checkFloating(): void {
    const {top} = this.floatSection.nativeElement.getBoundingClientRect();

    this.floating = top <= 0;

    // since this function is out of Angular detection zone, need to change class manually
    // from bmc-ux/adapt-angular#972
    if (this.floating) {
      this.floatWrapper.nativeElement.classList.add('a-floating');
    } else {
      this.floatWrapper.nativeElement.classList.remove('a-floating');
    }

    // there a zone of 145px under navigation, after scrolling it browser doesn't redraw upper part
    // eslint-disable-next-line yoda
    this.bizarreZone = 0 > top && top > -145;

    this.floatSection.nativeElement.classList.remove('a-margin-transition');

    this.setMargin();
  };

  private _loaderSubscriptionHandler(animationState: LoaderState): void {
    this.loadingRun = animationState.show;
    this.loadingMessage = animationState.message;
    this._changeDetectorRef.detectChanges();
  }

  private _alertSubscriptionHandler(newAlert: AlertAnimatedConfig): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptNavigationAlert, {
      EventType: AdaptRadarAngularCustomEventType.Init,
      Type: newAlert.type,
      Title: newAlert.title
    });

    this.alerts.unshift(newAlert);
  }

  private _getSiblingsCount(): number {
    return this.menu ? this.menu.filter(menuItem => !menuItem.hide).length : 0;
  }

  private _getHiddenSiblingsCount(): number {
    return this.menu ? this.menu.filter(menuItem => menuItem.hide).length : 0;
  }

  generateMenuItemTestID(item: MenuItem): string {
    if (!item.testID) {
      item.testID = `navigation-menu-item-test-id-${this._menuTestIdIndex++}`;
    }

    return item.testID;
  }
}
